.header__top {
  background-color: #001514;
  opacity: 0.6;
  z-index: 5;
  position: absolute;
  width: 1902px;
  padding: 32px 55px;
}

.language__russian,
.language__english {
  font-family: "Mont";
  font-weight: 400;
  font-size: 14px;
  color: #fdd099;
  border: 1px solid #ffffff;
  padding: 12px 12px;
  border-radius: 50%;
  opacity: 0.33;
  width: 40px;
  height: 40px;
  text-transform: uppercase;
  margin-left: 19px;

  &:hover {
    opacity: 100%;
  }

  &:active {
    border-color: #bd4f6c;
    color: #bd4f6c;
  }
}

.--language-active {
  opacity: 100%;
}

.header__top-clock {
  color: #fdd099;
  font-family: "Mont";
  font-weight: 600;
  font-size: 37px;
}

.header__top-mail {
  font-family: "Mont";
  font-weight: 400;
  color: #fdd099;
  text-decoration: underline;
  position: relative;
  top: 0;
  left: 0;

  &::before {
    content: url("../img/Mail_Circle.svg");
    width: 40px;
    height: 40px;
    border: 1px solid #ffffff;
    padding: 12px 10px;
    border-radius: 50%;
    position: absolute;
    top: -10px;
    left: -55px;
  }

  &:hover {
    color: #ffffff;
    text-decoration-color: #ffffff;
  }

  &:active {
    color: #bd4f6c;
    text-decoration-color: #bd4f6c;
  }
}

.header__top-authorization {
  padding: 20px 66px;
  font-family: "Mont";
  font-weight: 400;
  color: #001514;
  font-size: 16px;
  background-color: #fdd099;
  border: none;
  cursor: pointer;

  &:hover {
    border-radius: 25px;
    transition: ease-in-out 0.6s;
  }
}

.menu {
  
  &__list {
    display: inline-flex;
    background: #ffffff;
    list-style: none;
    position: absolute;
  bottom: 290px;
  left: -290px;
  z-index: 6;
  transform: rotate(-90deg);
  padding: 41px 38px;
  
  &-item {
    margin-left: 20px;
  }

  &-link {
    color: #001514;
    font-family: 'Mont';
    font-weight: 400;
    font-size: 16px;
  }
  }
}

swiper-container {
  width: 100%;
  height: 100%;
}

swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover; 
}

.swiper-pagination {
  width: 25px;
  height: 25px;
  color: #fff;
  background: #000;
}
